import Editor from '@monaco-editor/react';

const TextField = ({value = '', onChange}) => (
  <input type="text" value={value} onChange={(e) => onChange(e.target.value)} />
);

const NumberField = ({value = '', onChange}) => (
  <input
    type="number"
    value={value}
    onChange={(e) => onChange(e.target.value)}
  />
);

const Checkbox = ({value = false, onChange}) => (
  <input
    type="checkbox"
    checked={value}
    onChange={(e) => onChange(e.target.checked)}
  />
);

const Select = ({value, onChange, options}) => (
  <select value={value} onChange={(e) => onChange(e.target.value)}>
    <option></option>
    {options.map(({label, value}) => (
      <option key={label} value={value}>
        {label}
      </option>
    ))}
  </select>
);

// TODO: add code validation
const Code = ({value, onChange}) => (
  <Editor
    height={400}
    width={800}
    theme="vs-dark"
    defaultLanguage="javascript"
    value={value}
    onChange={onChange}
  />
);

const inputs = {
  text: TextField,
  number: NumberField,
  checkbox: Checkbox,
  select: Select,
  code: Code,
};

export const FormThing = ({fields, data, onChange}) =>
  fields.map(({prop, label, type, info, show, options}) => {
    if (show && !show(data)) return null;
    const Field = inputs[type];
    return (
      <div key={prop} title={info}>
        <label>{label}</label>
        <Field
          value={data?.[prop]}
          onChange={(val) => onChange(val, prop)}
          options={options}
        />
      </div>
    );
  });
